.intro {
    display: flex;
    align-items: center;
    min-height: 40px;
    border-left: 5px solid rgba(11, 122, 231, 1);
    border-left: 5px solid var(--color-theme);
    padding-left: 0.8em;
    font-size: xx-large;
}

.line-1_7 {
    line-height: 1.7em;
    padding: 10px 0px;
}

.skills {
    padding: 5% 0;
}

.skills--header {
    font-family: "Poppins";
    font-size: xx-large;
    border-left: 5px solid rgba(11, 122, 231, 1);
    border-left: 5px solid var(--color-theme);
    padding-left: 0.8em;
    margin-bottom: 20px;
}

.skills-content {
    min-height: 200px;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: -20px;

    & > div{
        margin-bottom: 20px;
    }
}

.skills--intro {
    flex: 1 0 35%;
    font-size: x-large;
    display: flex;
    font-family: "Roboto";
    color: var(--color-theme);
    flex-direction: column;
    justify-content: center;
    max-width: 320px;

    @media screen and (max-width: 800px) {
        text-align: center;
    }

    * {
        margin: 10px 0px;
    }

    & .skills--download_resume {
        cursor: pointer;
        color: white;
        background-color: var(--color-theme);
        border: none;
        border-radius: 20px;
        width: 10em;
        height: 35px;
    }
}

.skills--list {
    flex: 1 1 70%;
    display: flex;
    flex-wrap: wrap;

    & > div {
        margin-left: 20px;

        @media screen and (max-width: 500px) {
            margin-left: 0px;
        }
    }
}
